export default(params)=>{
	uni.showLoading({
		title:""
	});
	return new Promise((resolve,reject)=>{
		wx.request({
			...params,
			success(res) {
				resolve(res.data);
			},
			header:{
				'content-type':'application/json'
			},
			fail(err) {
				reject(err);
			},
			complete() {
				uni.hideLoading();
			}
		});
		
	});
}